<template>
  <LoadingVue :active="isLoading"></LoadingVue>
  <nav class="navbar navbar-expand-lg bg-secondary" data-bs-theme="dark">
    <div class="container-fluid">
      <router-link class="navbar-brand" to="/home"
          ><h1 class="fs-3">
            macar<i class="bi bi-egg-fill"></i>n
          </h1></router-link
        >
      <button
        class="navbar-toggler"
        type="button"
        data-bs-toggle="collapse"
        data-bs-target="#navbarNav"
        v-if="isLogin"
      >
        <span class="navbar-toggler-icon"></span>
      </button>
      <div class="collapse navbar-collapse" id="navbarNav" v-if="isLogin">
        <ul class="navbar-nav d-flex me-auto">
          <li class="nav-item">
            <router-link class="nav-link" to="/dashboard/products"
              >產品列表</router-link
            >
          </li>
          <li class="nav-item">
            <router-link class="nav-link" to="/dashboard/coupons"
              >優惠券列表</router-link
            >
          </li>
          <li class="nav-item">
            <router-link class="nav-link" to="/dashboard/orders"
              >訂單列表</router-link
            >
          </li>
        </ul>
        <button class="btn btn-outline-light" type="button" @click="logout">
          登出
        </button>
      </div>
    </div>
  </nav>
</template>

<script>
import emitter from '@/methods/emitter'
export default {
  data() {
    return {
      isLoading: false
    }
  },
  props: ['isLogin'],
  methods: {
    logout() {
      this.isLoading = true
      const API_LOGOUT = `${process.env.VUE_APP_API}logout`
      this.axios.post(API_LOGOUT).then((res) => {
        this.isLoading = false
        this.$router.push('/login')
      }).catch(e => {
        emitter.emit('sendMsg', { message: `登出失敗(${e})`, status: 'error' })
      })
    }
  }
}
</script>
